<template>
     <div class="login">
        <img src="../assets/resturant.jpg" class="logo" alt="">
        <form >
        <div class="form-control">
        <label for="name">Email:</label>
        <input type="text" v-model="email" placeholder="Enter Email..">
        </div>
        <div class="form-control">
        <label for="name">Password:</label>
        <input type="password" v-model="password" placeholder="Create a Password">
        </div>
       <input type="submit" value="Login" class="btn" @click="Login()" >
    </form>
    <p>Don't Have an account? <router-link class="signup" to="/signUp">SignUp</router-link></p> 
     </div>
</template>
<script>
import axios from 'axios'
export default {
  name:"loginPage",
  data(){
    return{
        email:'',
        password:'',
    }
  },
  methods:{
    async Login(){
       let result = await axios.get(`http://localhost:3000/users?email=${this.email}&password=${this.password}`)
       console.log(result)
       if(result.status === 200 && result.data.length > 0 && result.data[0].email != ''){
        localStorage.setItem('user-info',JSON.stringify(result.data[0]))
        this.$router.push({ name: 'home'})
       }else{
        this.$router.push({name: 'signUp'})
       }
    }
  
  },
  mounted(){
    const user = localStorage.getItem('user-info')
    if(user){
        this.$router.push({name: 'home'})
    }
  }
}
</script>

<style scoped>
.logo{
    width: 200px;
    height: 200px;
    border-radius: 50%;
}
form{
    max-width: 500px;
    margin: auto;
    padding: 20px;
}
.form-control{
    margin: 10px 0px;
}
.form-control label{
    display: block;
    text-align: left;
    font-size: 18px;
    font-weight: 600;
    font-family: 'Courier New', Courier, monospace;
    margin-bottom: 5px;
    color:black;
}
.form-control input{
    width: 100%;
    font-size: 18px;
    font-weight: 600;
    color: black;
    height: 40px;
    border: 2px solid black;
    margin: 5px 0px;
    border-radius: 5px;
    padding: 5px 0px 5px 10px;
    display: block;
}

.signup{
  text-decoration: none;
  font-size: 18px;
  font-weight: 600;
  padding-bottom: 50px;
  color: blue;
}
p{
  font-size: 18px;
  font-weight: 500;
}
</style>