<template>
    <div class="update">
        <headerPage/>
        <h1>Hello {{name}}, Welcome on Update Resturant Page</h1>
        <form action="">
            <div class="form-control">
                <label for="">Name</label>
                <input type="text" v-model="resturant.name" placeholder="Enter Name...">
            </div>
            <div class="form-control">
                <label for="">Address</label>
                <input type="text" v-model="resturant.address"  placeholder="Enter Address...">
            </div>
            <div class="form-control">
                <label for="">Contact</label>
                <input type="text" v-model="resturant.contact"  placeholder="Enter Contact...">
            </div>
            <button type="button" class="btn" @click="update()">Update Resturant</button>
        </form>
    </div>
</template>

<script>
import headerPage from '@/components/headerPage.vue'
import axios from 'axios'
export default {
    name:'updateRest',
    components:{
        headerPage
    },
    data(){
        return{
            name:'',
       resturant:{
        name:'',
        address:'',
        contact:''
       }
    }
   },
    methods:{
        async update(){
            const result = await axios.put('http://localhost:3000/resturants/'+ this.$route.params.id,{
                name: this.resturant.name,
                address: this.resturant.address,
                contact: this.resturant.contact
            })
            if(result.status === 200){
                this.$router.push({name: 'home'})
            }
            console.log('result:',result)
            console.log('why')
        }
    },
    async mounted(){
        let user = localStorage.getItem('user-info')
        this.name = JSON.parse(user).name
        console.log(this.$route.params.id)
        let result = await axios.get('http://localhost:3000/resturants/'+this.$route.params.id)
        console.log(result.data)
        this.resturant.name = result.data.name,
        this.resturant.address = result.data.address,
        this.resturant.contact = result.data.contact

    }
}
</script>

<style  scoped>
h1{
  font-size: 22px;
  font-family: 'Courier New', Courier, monospace;
  margin: 0;
  letter-spacing: 1px;
  margin-top: 10px;
}
</style>
