<template>
    <div id="sign">
        <img class="logo" src="../assets/resturant.jpg" alt="">
    <form >
        <div class="form-control">
        <label for="name">Name:</label>
        <input type="text" v-model="name" placeholder="Enter Name..">
        </div>
        <div class="form-control">
        <label for="name">Email:</label>
        <input type="text" v-model="email" placeholder="Enter Email..">
        </div>
        <div class="form-control">
        <label for="name">Password:</label>
        <input type="password" v-model="password" placeholder="Create a Password">
        </div>
       <input type="submit" value="Sign Up" class="btn" @click="addSubmit()">
    </form>
    <p>Already Have an account? <router-link class="login" to="/loginPage">Login</router-link></p> 
    </div>

</template>
<script>
import axios from 'axios'
export default {
   name: 'signUp',
   data () {
    return{
        name : '',
        email : '',
        password : ''
    }
   },
   methods : {
    async addSubmit()
    {
        const result = await axios.post('http://localhost:3000/users',{
            name: this.name,
            email: this.email,
            password:this.password
        })
        if(result.status === 201 && this.name != ''){
            // alert('User Sign-Up successfully')
            localStorage.setItem('user-info',JSON.stringify(result.data))
            this.$router.push({ name: 'home'})
        }
    }
   },
   mounted() {
  let user = localStorage.getItem('user-info')
  if(user){
    this.$router.push({name: 'home'})
  }
  }
}
</script>

<style scoped>
.logo{
    width: 200px;
    height: 200px;
    border-radius: 50%;
}
form{
    max-width: 500px;
    margin: auto;
    padding: 20px;
}
.form-control{
    margin: 10px 0px;
}
.form-control label{
    display: block;
    text-align: left;
    font-size: 18px;
    font-weight: 600;
    font-family: 'Courier New', Courier, monospace;
    margin-bottom: 5px;
    color:black;
}
.form-control input{
    width: 100%;
    font-size: 18px;
    font-weight: 600;
    color: black;
    height: 40px;
    border: 2px solid black;
    margin: 5px 0px;
    border-radius: 5px;
    padding: 5px 0px 5px 10px;
    display: block;
}
.btn{
    width: 103%;
    font-size: 18px;
    font-weight: 600;
    color:white;
    height: 50px;
    border: 2px solid black;
    border-radius: 5px;
    padding: 15px;
    background: black;
    display: block;
    margin-top: 20px;
    cursor: pointer;
}
.login{
  text-decoration: none;
  font-size: 18px;
  font-weight: 600;
  padding-bottom: 50px;
  color: blue;
}
p{
  font-size: 18px;
  font-weight: 500;
}
</style>
