<template>
  <div class="home" >
    <headerPage></headerPage>
  <h1>Hello {{name}}, Welcome to the home page.I am your new tour</h1>

  </div>
  <table>
    <tr>
      <th>Id</th>
      <th>Company Name</th>
      <th>Contact</th>
      <th>Address</th>
      <th>Actions</th>
      </tr>
      <tr v-for="item in resturant" :key="item.id">
    <td>{{item.id}}</td>
    <td>{{item.name}}</td>
    <td>{{item.contact}}</td>
    <td>{{item.address}}</td>
    <td><router-link :to="'/updateRest/'+item.id">Update</router-link>
      <button @click="deleted(item.id)" >Delete</button>
    </td>
    
    </tr>
  </table>
</template>

<script>
// @ is an alias to /src
import axios from 'axios'
import headerPage from '@/components/headerPage'
export default {
  name: 'HomeView',
  components:{
    headerPage,
  },
  data(){
    return{
      name:'',
      resturant:[]
    }
  },
  methods:{
    async deleted(id){
      console.warn(id)
      let result = await axios.delete('http://localhost:3000/resturants/'+id )
      console.log(result)
      if(result.status === 200){
        this.loadData()
      }
    },
      async loadData(){
          let user = localStorage.getItem('user-info')
  this.name = JSON.parse(user).name
  let result = await axios.get('http://localhost:3000/resturants')
    this.resturant = result.data
      }
    
  },
  async mounted() {
  let user = localStorage.getItem('user-info')
  this.name = JSON.parse(user).name
  let result = await axios.get('http://localhost:3000/resturants')
    this.resturant = result.data
  }

}
</script>

